import React, { useState } from 'react'
import './ScriptInputModule.css';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';



export default function ScriptInputModule({submitScript, submitSelectedWords, PlaybackScript}) {

    const [stringScript, UpdateStringScript] = useState(''); // stores the text input of the text field. The 'UpdateStringScript' function is triggered every time the text field's text changes
    const [wordCount, UpdateWordCount] = useState(0); // stores the word count of the text field in 'wordCount'. The 'UpdateWordCount' function is triggered every time the text field's text changes


    // ----- function recieves a string and is expected to return an array of each individual word of the string -----
    const SplitScript = (stringScript) => {

        // If 'stringScript' is not a string send a log message to the console and end this function immediately
        if (stringScript === false) { console.log('no string recieved for array conversion'); return; }

        // replace any line breaks in 'stringScript' with a " " (replace), split 'stringScript' where a " " is found (split), then remove any "" elements from 'arrayScript' (trim)
        const arrayScript = stringScript.replace(/\r?\n|\r/g, ' ').split(' ').filter(entry => entry.trim() !== '');

        // Return the recieved script as an array of each word of the 'stringScript' string
        return (arrayScript);
    }
    // ----------------------------------------------------------------------------------------------------------------


    // ----- function is triggered every time a mouse button is released while the mouse cursor is overlapping the text field -----
    const GetSelection = (selectedText) => {

        // get the input text string from the text field and isolate and copy the selected text string as 'selectedWords'
        const selectedWords = selectedText.value.substr(selectedText.selectionStart, selectedText.selectionEnd - selectedText.selectionStart);

        submitSelectedWords(selectedWords); // send 'selectedWords' to the parent component via 'submitSelectedWords' (use state function)
    }
    // ----------------------------------------------------------------------------------------------------------------------------


    // ----- function is triggered every time a mouse button is pressed while the mouse cursor is overlapping the text field -----
    const ClearSelection = () => {

        // check to see if any text is selected. If text is selected, deselect it
        if (window.getSelection) { window.getSelection().removeAllRanges(); }
        else if (document.selection) { document.selection.empty(); }

    }
    // ---------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered when the 'Preview' button is clicked ------------------------------
    const PreviewScript = (stringToPreview) => {

        if ((stringToPreview.trim()).length === 0) return;  // if the number of characters in the 'stringToPreview' string is 0 AFTER all empty and space characters have been removed, end this function at this point

        PlaybackScript(stringToPreview, document.getElementById('previewButton-script')); // trigger the 'PlaybackSpeech' function on the parent component via 'PlaybackScript'
    }
    // -------------------------------------------------------------------------------------------------------------------------



    return (

        <Box id="boxWrapper-script" className="sectionWrapper">



            <Grid className="sectionHeading" container> {/* --------------------------------- MODULE TITLE CONTAINER  --------------------------------- */}


                <Grid item xs={11}>

                    {/* -------------------- module title -------------------- */}
                    <h4>Master Script View</h4>
                    {/* ------------------------------------------------------ */}

                </Grid>

                <Grid item xs={1}>

                    {/* -------------------- preview script button -------------------- */}
                    <Button
                        id="previewButton-script"
                        className="previewButton button smallButton"
                        variant="contained"

                        onClick={() => {
                            PreviewScript(stringScript);
                        }}
                    > Preview </Button>
                    {/* --------------------------------------------------------------- */}

                </Grid>


            </Grid> {/* -------------------------------------- MODULE TITLE CONTAINER  -------------------------------------- */}



            <TextField /* --------------------------------- INPUT SCRIPT TEXT FIELD  --------------------------------- */
                id="inputTextField-script"
                fullWidth
                multiline
                rows={9}
                placeholder="Type your script here"
                
                onChange={(e) => [submitScript(e.target.value), UpdateWordCount(SplitScript(e.target.value).length), UpdateStringScript(e.target.value)]}
                onMouseDown={ClearSelection}
                onMouseUp={(e) => GetSelection(e.target)}
            /> {/* ------------------------------------ INPUT SCRIPT TEXT FIELD  ------------------------------------ */}



            <Grid container id='sectionFooter-script'> {/* --------------------------------- MODULE FOOTER CONTAINER  --------------------------------- */}

                {/* --------------------------- script word count display --------------------------- */}
                <Grid item xs={6} id="wordCount-script"> Word Count: {wordCount} words </Grid>
                {/* --------------------------------------------------------------------------------- */}

                {/* --------------------------- script approximate length display --------------------------- */}
                <Grid item xs={6} id="scriptLength-script"> Approximate Script Length: 00.0 s </Grid>
                {/* ----------------------------------------------------------------------------------------- */}


            </Grid> {/* ------------------------------------- MODULE FOOTER CONTAINER  ------------------------------------- */}



        </Box>
    )
}