import './SelectMenu.css';
import { useState, useEffect, useRef } from 'react';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

export default function SelectMenu({ reset, enabled, options, defaultText, GetSelectionValue }) {

    const [selection, SetSelection] = useState('');

    useEffect(() => { ResetMenu_Ref.current(); }, [reset, enabled])



    function ResetMenu() {

        SetSelection('');
        GetSelectionValue('');
        options = null;
    }
    const ResetMenu_Ref = useRef();
    ResetMenu_Ref.current = ResetMenu;



    return (



        <FormControl className='selectMenu-form'>

            <Select
                className='selectMenu-select'
                displayEmpty
                value={selection}
                disabled={!enabled}
                
                onChange={(e) => [SetSelection(e.target.value), GetSelectionValue(e.target.value)]}
            >

                <MenuItem value="">{defaultText}</MenuItem>

                {options}
                
            </Select>
        </FormControl>



    )
}