import './VerticalSlider.css';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';

export default function VerticalSlider() {

    // prevents the default behaviour of this selection slider when the left or right arow keyboard keys are pressed down. function is triggered every time a keyboard key is pressed down while focusing on this selection slider
    function preventHorizontalKeyboardNavigation(event) { 
        if (event.key === 'ArrowLeft' || event.key === 'ArrowRight') { // If the left or right arow keyboard keys are pressed down...
            event.preventDefault(); //prevents the default behaviour of this selection slider when focused
        }
    }
    // ---------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------

    return (



        <Box sx={{ height: 100 }}> 

            {/* -------------------- selection slider -------------------- */}
            <Slider id='slider'
                sx={{
                    '& input[type="range"]': {
                        WebkitAppearance: 'slider-vertical',
                    },
                }}
                orientation="vertical"
                step={1}
                marks
                min={1}
                max={4}


                onKeyDown={preventHorizontalKeyboardNavigation}
            />
            {/* ---------------------------------------------------------- */}

        </Box>



    )
}