import './TopMenuModule.css';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';

export default function TopMenuModule() {

    return (

        <Grid container id='gridContainer-top'>



            <Grid item xs={6}> {/* ---------------------------- LEFT MENU ---------------------------- */}
                               
                <Stack id='leftMenu-top' direction="row" spacing={1}>

                    {/* -------------------- open button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Open </Button></div>
                    {/* ----------------------------------------------------- */}

                    {/* -------------------- save button -------------------- */}     
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Save </Button></div>
                    {/* ----------------------------------------------------- */}

                    {/* -------------------- print button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Print </Button></div>
                    {/* ------------------------------------------------------ */}

                </Stack>
            </Grid> {/* ---------------------------- LEFT MENU ---------------------------- */}


            <Grid item xs={6}> {/* ---------------------------- RIGHT MENU ---------------------------- */}
                <Stack id='rightMenu-top' direction="row" spacing={1}>

                    {/* -------------------- submit button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Submit </Button></div>
                    {/* ------------------------------------------------------- */}

                    {/* -------------------- options button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > Options </Button></div>
                    {/* -------------------------------------------------------- */}

                    {/* -------------------- user button -------------------- */}
                    <div><Button
                        className="button mediumButton"
                        variant="contained"

                        onClick={null}
                    > User </Button></div>
                    {/* ----------------------------------------------------- */}

                </Stack>
            </Grid> {/* ---------------------------- RIGHT MENU ---------------------------- */}



        </Grid> 
    )
}