import './App.css';
import { useState } from 'react';
import TopMenuModule from './components/TopMenuModule/TopMenuModule';
import ScriptInputModule from './components/ScriptInputModule/ScriptInputModule';
import VoicePickerModule from './components/VoicePickerModule/VoicePickerModule';
import EditByWordModule from './components/EditByWordModule/EditByWordModule';
import DownloadAudioBtn from './components/DownloadAudioBtn/DownloadAudioBtn';
import Title from './components/Title/Title';
import Stack from '@mui/material/Stack';



export default function App() {

    const AWS = require('aws-sdk'); // initiate the Amazon Web Services SDK
    AWS.config.region = 'ca-central-1'; // configure the Amazon Web Services region to the nearest region
    // configure the Amazon Web Services credentials to a random access key ID & a random secret access key generated by the AWS Cognito service
    AWS.config.credentials = new AWS.CognitoIdentityCredentials({ IdentityPoolId: 'ca-central-1:bf4fdb3d-b7e1-4453-a8f4-647aa689377f', });

    // set up an Amazon Polly profile using the Amazon Web Services configuration and credentials set in 'AWS'
    const AWS_polly = new AWS.Polly({
        region: AWS.config.region,
        accessKeyId: AWS.config.accessKeyId,
        secretAccessKey: AWS.config.secretAccessKey,
    })

    const audioPlayer = new Audio(); // controls all returned text to speech audio

    const [receivedScript, receiveScript] = useState('') // recieves, holds, and sends the script string to and from child components
    const [receivedWords, receiveSelectedWords] = useState('') // recieves, holds, and sends the selected string to and from child components

    const [voiceSelectionEngine, ReceiveVoiceSelectionEngine] = useState(''); // recieves and holds, and sends the voice selected ID to and from child components
    const [voiceSelectionID, ReceiveVoiceSelectionID] = useState(''); // recieves and holds, and sends the voice selected engine to and from child components

    var activePreviewButton; // keep track of the most recent 'preview button' clicked


    // ------------- function is triggered when the audio player naturally stops playing -------------
    audioPlayer.onended = function () {

        // change the 'activePreviewButton' label to 'Preview' and clear the 'activePreviewButton'
        activePreviewButton.innerText = 'Preview';
        activePreviewButton = null;

    };
    // --------------------------------------------------------------------------------------

    // ---------------------- function is triggered every time a 'preview button' is clicked ----------------------
    function PlaybackSpeech(stringSpeech, button) {

        audioPlayer.pause(); // stop the audio player if it is currently playing

        // if no 'stringSpeech' has been given, or no 'voiceSelectionID' or 'voiceSelectionEngine' is currently set, or 'button' has not been given, end this function at this point
        if (stringSpeech === null || (stringSpeech.trim()).length === 0 || voiceSelectionID === "" || voiceSelectionEngine === "" || button === null) { return; }

        // --- variables Amazon Polly requires to generate speech from text ---
        const input = {
            Text: stringSpeech, // the text to convert to speech
            OutputFormat: "mp3", // the output file type
            VoiceId: voiceSelectionID, // the Amazon playback voice
            Engine: voiceSelectionEngine, // the Amazon speech engine used to translate text to speech
        }
        // ---------------------------------------------------------------------

        // if the 'activePreviewButton' is the 'button' element,  change the 'activePreviewButton' label to 'Preview', clear the 'activePreviewButton', and end this function at this point
        if (activePreviewButton === button) { button.innerText = 'Preview'; activePreviewButton = null; return; }

        activePreviewButton = button; // set the 'activePreviewButton' to the 'button' element


        AWS_polly.synthesizeSpeech(input, (err, data) => { // send a generate speech from text request to Amazon Polly

            // if the request to Amazon Polly could/was not receieved or speech data could/was not returned, log the specific encountered error to the console and end this function at this point
            if (err) { console.log(err); return; }

            // --- convert the returned speech data into an audio format the audio player can read and play ---
            const uInt8Array = new Uint8Array(data.AudioStream);
            const arrayBuffer = uInt8Array.buffer;
            const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
            const url = URL.createObjectURL(blob);
            // -------------------------------------------------------------------------------------------------

            // set the audio player's source to the readable speech data and play the audio player
            audioPlayer.src = url;
            audioPlayer.play();

            for (var i = 0; i < document.getElementsByClassName('previewButton').length; i++) { // cycles through every element with the 'previewButton' class attached

                if (document.getElementsByClassName('previewButton')[i] === button) { button.innerText = 'Stop'; } // if the found element is the 'button' element, change 'button' label to 'Stop'

                else { document.getElementsByClassName('previewButton')[i].innerText = 'Preview'; } // or else, change the found elements label to 'Preview'

            }
        })


            
    }
    // ------------------------------------------------------------------------------------------------------------

    // ------------------------------ function is triggered when the 'Download Audio' button is clicked ------------------------------
    function GenerateSpeechFile(stringSpeech) {

        // if no 'stringSpeech' has been given, or no 'voiceSelectionID' or 'voiceSelectionEngine' is currently set, end this function at this point
        if (stringSpeech === null || (stringSpeech.trim()).length === 0 || voiceSelectionID === "" || voiceSelectionEngine === "") { return; }

        // --- variables Amazon Polly requires to generate speech from text ---
        const input = {
            Text: stringSpeech, // the text to convert to speech
            OutputFormat: "mp3", // the output file type
            VoiceId: voiceSelectionID, // the Amazon playback voice
            Engine: voiceSelectionEngine, // the Amazon speech engine used to translate text to speech
        }
        // ---------------------------------------------------------------------


        AWS_polly.synthesizeSpeech(input, (err, data) => { // send a generate speech from text request to Amazon Polly

            // if the request to Amazon Polly could/was not receieved or speech data could/was not returned, log the specific encountered error to the console and end this function at this point
            if (err) { console.log(err); return; }

            // --- convert the returned speech data into an audio format the audio player can read and play ---
            const uInt8Array = new Uint8Array(data.AudioStream);
            const arrayBuffer = uInt8Array.buffer;
            const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
            const url = URL.createObjectURL(blob);
            // -------------------------------------------------------------------------------------------------


            // --- generates and downloads the requested audio file, the audio file is saved to the default location specificed by the browser ---
            const DownloadAudioFile = async (url) => {

                const downloader = document.createElement('a'); // create an HTML link element

                downloader.download = 'speech-playback.mp3'; // name of the file to be generated
                downloader.href = url; // link to the readable speech data

                // a click event for the HTML link element that triggers when the 'DownloadAudioFile' is executed
                downloader.addEventListener('click', (e) => { setTimeout(() => URL.revokeObjectURL(downloader.href), 30 * 1000); }); // this event sends the generate and download file request to Amazon Polly
                downloader.click();

                downloader.remove(); // remove the HTML link element
            };
            DownloadAudioFile(url); // trigger the 'DownloadAudioFile' function using the readable speech data as a parameter
            // --------------------------------------------------------------------------------------------------------------------------------------

        })
    }
    // -------------------------------------------------------------------------------------------------------------------------------



    return (

        <>

            <Stack id='topUI-app'direction="row">

                <div id='topUI-left-app'>
                </div>

                <div id='topUI-center-app'>

                    <TopMenuModule /* renders  the 'TopMenuModule' compomnent */ />

                    <ScriptInputModule submitScript={receiveScript} submitSelectedWords={receiveSelectedWords} PlaybackScript={PlaybackSpeech}
                    /* renders  the 'ScriptInputModule' compomnent,
                     * recieves the submmited script from the 'ScriptInputModule' compomnent,
                     * shares the 'ScriptInputModule' compomnent's 'SubmitSelectedWords' function,
                     * shares 'PlaybackSpeech' with the 'ScriptInputModule' component */ />

                </div>

                <VoicePickerModule AWS_polly={AWS_polly} SubmitVoiceSelectionEngine={ReceiveVoiceSelectionEngine} SubmitVoiceSelectionID={ReceiveVoiceSelectionID} id='topUI-right-app'
                /* renders the 'VoicePickerModule' compomnent,
                 * share 'AWS_polly' with the 'VoicePickerModule' component,
                 * recieves the  voice selection ID from the 'VoicePickerModule' component */ />

            </Stack>

            <EditByWordModule selectedWords={receivedWords} PlaybackSelection={PlaybackSpeech}
            /* renders  the 'EditByWordModule' compomnent,
             * sends the selected words string to the 'EditByWordModule' component,
             * shares 'PlaybackSpeech' with the 'EditByWordModule' component */ />

            <DownloadAudioBtn submittedScript={receivedScript} RequestSpeechFile={GenerateSpeechFile}
            /* renders  the 'DownloadAudioBtn' compomnent,
             * share the recieved script with the 'DownloadAudioBtn' component,
             * shares the 'DownloadAudioBtn' component's 'RequestSpeechFile' function */ />

            <Title /* renders  the 'Title' compomnent */ />



        </>
    )

}
