import './EditByWordModule.css';
import { useState } from 'react';
import { useEffect } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import VerticalSlider from '../VerticalSlider/VerticalSlider';

export default function EditByWordModule({selectedWords, PlaybackSelection}) {

    const [displayWords, SetdisplayWords] = useState([]) // stores the string array of split selected words in 'displayWords'. The 'SetdisplayWords' function is triggered every time the 'SplitSelectedWords' function is triggered


    // ------------------------------------------------------------ Triggers every time the value of 'selectedWords' is changed ------------------------------------------------------------
    useEffect(() => {

        // ------------------------------ function is triggered every time the value of 'selectedWords' is changed ------------------------------
        const SplitSelectedWords = (stringSelectedWords) => {

            // If 'stringSelectedWords' is not a string send a log message to the console and end this function immediately
            if (stringSelectedWords === false) { console.log('no string recieved for array conversion'); return; }

            // replace any line breaks in 'stringSelectedWords' with a " " (replace), split 'stringSelectedWords' where a " " is found (split), then remove any "" elements from 'arraySelectedWords' (trim)
            const arraySelectedWords = stringSelectedWords.replace(/\r?\n|\r/g, ' ').split(' ').filter(entry => entry.trim() !== '');

            // slice limits the amount of words (elements) stored in 'arraySelectedWords'
            arraySelectedWords.slice(0, 10);

            // cycle through all of elements in 'arraySelectedWords' and remove all non alphanumeric ( and exceptions) characters from each string
            for (var i = 0; i < arraySelectedWords.length; i++) { arraySelectedWords[i] = arraySelectedWords[i].replace(/[^A-Za-z0-9-/]/g, ''); }

            // Update the 'DisplayWords' array to copy the 'ArraySelectedWords' array
            SetdisplayWords(arraySelectedWords);
        }
        // --------------------------------------------------------------------------------------------------------------------------------------
        SplitSelectedWords(selectedWords); // trigger the 'SplitSelectedWords' function

    }, [selectedWords])
    // -------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------


    // ------------------------------ function is triggered when the 'Preview' button is clicked ------------------------------
    const PreviewSelection = (stringToPreview) => {

        if ((stringToPreview.trim()).length === 0) return; // if the number of characters in the 'string to preview' text is 0 AFTER all empty and space characters have been removed, end this function at this point

        PlaybackSelection(stringToPreview, document.getElementById('previewButton-selection')); // trigger the 'PlaybackSpeech' function on the parent component via 'PlaybackSelection'
    }
    // -------------------------------------------------------------------------------------------------------------------------



    return (

        <Box id="boxWrapper-selection" className="sectionWrapper">



            <Grid className="sectionHeading" container> {/* --------------------------------- MODULE TITLE CONTAINER  --------------------------------- */}

                
                <Grid item xs={11}>

                    {/* -------------------- module title -------------------- */}
                    <h4>Selection View</h4>
                    {/* ------------------------------------------------------ */}

                </Grid>
                

                <Grid item xs={1}>

                    {/* -------------------- preview selection button -------------------- */}
                    <Button
                        id="previewButton-selection"
                        className="previewButton button smallButton"
                        variant="contained"

                        onClick={() => {
                            PreviewSelection(selectedWords);
                        }}
                    > Preview </Button>
                    {/* ------------------------------------------------------------------ */}

                </Grid>


            </Grid> {/* -------------------------------------- MODULE TITLE CONTAINER  -------------------------------------- */}


            {/* ------------------------------------------------- EDIT BY WORD INTERFACE TABLE ------------------------------------------------- */}
            <Table id='table-selection'>

                <TableHead id='tableHead-selection'>
                    <TableRow>

                        {/* ---------- previous selected word button ---------- */}
                        <TableCell className='tableCell-selection-cap' align="center">&#60;&#60;</TableCell>
                        {/* --------------------------------------------------- */}

                        {/* ----------------- selected word display's ----------------- */}
                        <TableCell className='tableCell-selection' align="center">{displayWords[0]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[1]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[2]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[3]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[4]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[5]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[6]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[7]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[8]}</TableCell>
                        <TableCell className='tableCell-selection' align="center">{displayWords[9]}</TableCell>
                        {/* ----------------------------------------------------------- */}

                        {/* ---------- next selected word button ---------- */}
                        <TableCell className='tableCell-selection-cap' align="center">&#62;&#62;</TableCell>
                        {/* ----------------------------------------------- */}

                    </TableRow>
                </TableHead>

                <TableBody id='tableBody-selection'>
                    <TableRow>

                        {/* ----------------- editing switches ----------------- */}
                        <TableCell className='tableCell-selection-cap' align="center">
                            <Stack id='editingSwitches-selection' spacing={0.25}>

                                <div><Button
                                    className=" button largeButton"
                                    variant="contained"

                                    onClick={null}
                                >Rate</Button></div>

                                <div><Button
                                    className=" button largeButton"
                                    variant="contained"

                                    onClick={null}
                                >Pitch</Button></div>

                                <div><Button
                                    className=" button largeButton"
                                    variant="contained"

                                    onClick={null}
                                >Volume</Button></div>

                                <div><Button
                                    className=" button largeButton"
                                    variant="contained"

                                    onClick={null}
                                >Emphasis</Button></div>

                            </Stack>
                        </TableCell>
                        {/* ---------------------------------------------------- */}

                        {/* ------------------------ sliders ------------------------ */}
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        <TableCell className='tableCell-selection' align="center"><VerticalSlider /></TableCell>
                        {/* --------------------------------------------------------- */}


                        {/* --------- slider level label's --------- */}
                        <TableCell className='tableCell-selection-cap' align="center">
                            <Stack id='levelLabels-selection' spacing={-2.5}>
                                <div><p> - Strong </p></div>
                                <div><p> - Moderate </p></div>
                                <div><p> - None </p></div>
                                <div><p> - Reduced </p></div>
                            </Stack>
                        </TableCell>
                        {/* -----------------------------------------*/}

                    </TableRow>
                </TableBody>

            </Table>
            {/* ------------------------------------------------- EDIT BY WORD INTERFACE TABLE -------------------------------------------------- */}



        </Box>
    )
}